@import "../../../scss/index.scss";

.signin-signup {
  &__left {
    overflow: hidden;
    min-height: 100vh;
    background-color: var(--primary-light);
    display: flex;
    align-items: center;
    justify-content: center;

    @media (max-width: 700px) {
      display: none;
    }

    img {
      margin-bottom: 350px;
      position: absolute;
      width: 400px;
      height: 400px;
    }

    div {
      z-index: 1;
      margin-top: 400px;

      h2 {
        display: flex;
        align-items: center;
        font-size: 20px;
        margin-bottom: 40px;
        font-weight: bold;
        color: var(--primary-dark);

        svg {
          margin-right: 20px;
          font-size: 30px;
          width: 40px;
        }
      }

    }
  }

  &__right {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var(--primary-dark);
    @media (max-width: 700px) {
      background-color: var(--primary);
      margin-top: 30px;
      margin-left: 25%;
    }

    div {
      width: 500px;
      margin: 0 auto;

      img {
        width: 150px;
        margin-bottom: 20px;
      }

      .firstTitle {
        font-size: 30px;
        font-weight: 600;
        margin-bottom: 20px;
        color: var(--icon-light);

        @media (max-width: 1000px) {
          font-size: 25px;
        }
        @media (max-width: 700px) {
          font-size: 20px;
          color: var(--font-light);
        }
        @media (max-width: 400px) {
          font-size: 15px;
        }
      }

      .secondTitle {
        font-size: 25px;
        font-weight: 600;
        margin-bottom: 40px;
        color: var(--icon-light);
        @media (max-width: 1000px) {
          font-size: 20px;
        }
        @media (max-width: 700px) {
          font-size: 15px;
          color: var(--font-light);
        }
        @media (max-width: 400px) {
          font-size: 10px;
        }
      }

      button {
        display: block;
        width: 80%;
        border-radius: 5px;
        font-size: 30px;
        font-weight: bold;
        padding: 10px 0;
        margin: auto;

        &:first-of-type {
          margin-bottom: 50px;
        }

        @media (max-width: 600px) {
          font-size: 20px;
        }
      }
    }
  }
}
.containerInfoSign {
  text-align: center;
}
.imgLogo{
  @media (max-width: 700px) {
   background-color: var(--primary-dark);
   border-radius: 20px;
  }
}