@import "../../../scss/index.scss";

.sign-up-form {
  h2 {
    font-weight: 450;
    padding-bottom: 30px;
    text-align: center;
    color: var(--icon-light);
  }
  label {
    color: var(--icon-light);
  }

  .form-group {
    padding-bottom: 20px;

    input,
    select {
      font-size: 25px;
      color: var(--icon-light) !important;

      @media (max-width: 1000px) {
        font-size: 15px;
      }

      @media (max-width: 500px) {
        font-size: 10px;
      }
    }
  }

  button {
    width: 50%;
    font-size: 20px;
    font-weight: bold;
    padding: 15px;
    border-radius: 50px;
    margin-top: 20px;
    margin-left: 25%;
    .spinner-boder {
      width: 25px;
      height: 25px;
      @media (max-width: 500) {
        width: 15px;
        height: 15px;
      }
    }
    @media (max-width: 700px) {
      font-size: 15px;
    }
    @media (max-width: 350px) {
      font-size: 10px;
    }
  }

  #Terms {
    margin-top: 10px;
    height: 150;
    width: 100%;
    overflow: auto;
    color: var(--icon-light);
  }
}
